.menu-sidebar.modal.fade .modal-dialog {
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}
.menu-sidebar.modal.fade {
  -webkit-transition: opacity 0.15s linear, -webkit-transform 0.3s ease-out;
  transition: opacity 0.15s linear, -webkit-transform 0.3s ease-out;
  -o-transition: transform 0.3s ease-out, opacity 0.15s linear;
  transition: transform 0.3s ease-out, opacity 0.15s linear;
  transition: transform 0.3s ease-out, opacity 0.15s linear, -webkit-transform 0.3s ease-out;
}
.menu-sidebar.modal.fade:not(.in).left {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.menu-sidebar.modal.fade:not(.in).right {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.menu-sidebar.modal {
  max-width: 100%;
  -webkit-box-shadow: 5px 0 10px;
          box-shadow: 5px 0 10px;
}
.menu-sidebar.modal.left {
  right: auto;
}
.menu-sidebar.modal.right {
  left: auto;
}
.menu-sidebar.modal .modal-dialog {
  margin: 0;
  width: auto;
}
.menu-sidebar.modal .modal-dialog .modal-content {
  background-color: transparent;
  border: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.menu-sidebar.modal .modal-dialog .modal-content .modal-body {
  padding: 0;
  /*ul li a:hover {*/
    /*border-left: 8px solid #f5934e;*/
    /*background-color: #182335;*/
  /*}*/
}
.menu-sidebar.modal.fade:not(.in).left .modal-dialog {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.menu-sidebar.modal.fade:not(.in).right .modal-dialog {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.menu-sidebar .nav {
  width: 320px;
}



